import axios from 'axios';

const axiosClient = axios.create({
  baseURL: process.env.VUE_APP_BASE + ':4000',
  headers: {
    'Content-Type': 'application/json'
  }
});
const API={
    getAll(table){
        const url=`${table}`;
        return axiosClient.get(url);
    },
    getAllCateAndNote(table){
        const url=`${table}`;
        console.log('url: '+ url);
        return axiosClient.get(url);
    },
    getLatestNode(table,id){
        const url=`${table}?cate_id=${id}&_sort=create_at&_order=desc&_limit=1`;
        return axiosClient.get(url);
    },
    getById(table,id){
        const url=`${table}/${id}`;
        return axiosClient.get(url);
    },
    add(table,product){ 
        const url=`${table}`;
        return axiosClient.post(url,product);
    },
    remove(table,id){
        const url=`${table}/${id}`;
        return axiosClient.delete(url);
    },
    update(table,id,data){
        const url=`${table}/${id}`;
        return  axiosClient.put(url,data);
    }
}
export default API;
