<template>
    <div class="container-fluid">
        <!-- Page Heading -->
        <!-- Content Row -->
        <div class="row">
            <!-- Earnings (Monthly) Card Example -->
            <div v-for="(system, index) in data" :key="index" class="col-xl-3 col-md-6 mb-4" data-toggle="modal"
                data-target="#exampleModalCenter">
                <div class="card border-left-primary shadow h-100 py-2">
                    <div class="card-body">
                        <div class="row no-gutters align-items-center">
                            <div class="col mr-2">
                                <div class="h5 mb-0 font-weight-bold text-gray-800">{{ system.name }}</div>
                                <hr>
                                <div class="mb-2"> Temperature: <b>{{ system.notes[0].temperature ? system.notes[0].temperature : 0 }}</b> <b>°C</b></div>
                                <div class="mb-2"> pressure: <b>{{ system.notes[0].pressure ? system.notes[0].pressure : 0 }}</b> <b>pa</b></div>
                                <div class="mb-2"> Altitude sea: <b>{{ system.notes[0].altitude_sea ? system.notes[0].altitude_sea : 0 }}</b> <b>m</b></div>
                                <div class="mb-2"> Altitude cm: <b>{{ system.notes[0].altitude_cm ? system.notes[0].altitude_cm : 0 }}</b> <b>cm</b></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <!-- Content Row -->

        </div>
    </div>
</template>
  
<script>
import BaseAPI from "../core/api/BaseApi"
export default {
    name: 'NodeComponent',
    data() {
        return {
            data: [],
        };
    },
    methods: {
        async fetchDataNote(cate_id) {
            const response = await BaseAPI.getLatestNode('Node', cate_id);
            return response.data;
        },
        async fetchData() {
            const response = await BaseAPI.getAllCateAndNote('categories');
            const promises = response.data.map((cate) => {
                return this.fetchDataNote(cate.id);
            });
            const allData = await Promise.all(promises);
            response.data.forEach((cate, index) => {
                cate.notes = allData[index];
            });
            return response.data;
        }
    },
    created() {
        this.fetchData()
            .then(data => {
                this.data = data;
                console.log('categories:', this.data[0].notes[0]);
            })
            .catch(error => {
                console.log('Error:', error);
            });
    }

}
</script>
  
